
.monitor-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(2px);
  overflow-y: auto;
}

.monitor-modal {
  background: #0a0a0a;
  border: 1px solid #00ff00;
  box-shadow: 0 0 15px rgba(0, 255, 0, 0.2);
  width: 90%;
  max-width: 500px;
  display: flex;
  flex-direction: column;
  max-height: 95vh;
  overflow-y: auto;
}

.modal-header {
  flex-shrink: 0;
  padding: 10px;
  background: #002200;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #004400;
  
  .modal-title {
    color: #00ff00;
    font-weight: bold;
  }
  
  .close-btn {
    background: none;
    border: none;
    color: #00ff00;
    font-size: 1.5rem;
    cursor: pointer;
    &:hover { color: #fff; }
  }
}

.modal-body {
  flex: 1;
  padding: 15px;
  display: flex;
  flex-direction: column;
  gap: 15px;
  overflow-y: auto;
}

.video-feed {
  aspect-ratio: 16/9;
  background: #000;
  position: relative;
  border: 1px solid #333;
  overflow: hidden;
  flex-shrink: 0;
  
  &.offline { border-color: #333; }
  
  .feed-overlay {
    position: absolute;
    top: 5px;
    left: 5px;
    right: 5px;
    display: flex;
    justify-content: space-between;
    color: #00ff00;
    font-size: 0.8rem;
    z-index: 2;
    text-shadow: 0 0 2px #000;
  }
  
  .rec-badge {
    color: #ff0000;
    animation: blink 1s infinite;
  }
  
  .feed-content {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #00aa00;
    font-family: 'Courier New', monospace;
  }
  
  .state-large {
    font-size: 1.5rem;
    font-weight: bold;
    text-shadow: 0 0 5px #00ff00;
    text-align: center;
  }
  
  .feed-offline {
    font-size: 1.5rem;
    color: #555;
    font-style: italic;
  }
  
  .scan-line {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background: rgba(0, 255, 0, 0.1);
    animation: scan 3s linear infinite;
  }
}

.data-panel {
  display: flex;
  flex-direction: column;
  gap: 8px;
  flex-shrink: 0;
}

.data-row {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 0.8rem;
  
  label { 
    width: 100px; 
    color: #888;
    flex-shrink: 0;
    &.danger-text { color: #ff4444; }
  }
  
  .progress-bar {
    flex: 1;
    height: 12px;
    background: #222;
    position: relative;
    border-radius: 6px;
    overflow: hidden;
    
    .fill {
      height: 100%;
      background: #00ff00;
      &.alert { background: #ffaa00; }
      &.danger { background: #ff0000; }
      transition: width 0.3s ease-in-out;
    }
    
    .value {
      position: absolute;
      right: 5px;
      top: 50%;
      transform: translateY(-50%);
      font-size: 0.7rem;
      color: #fff;
      text-shadow: 0 0 2px #000;
    }
  }
}

.action-panel {
  display: flex;
  gap: 10px;
  margin-top: 5px;
  flex-shrink: 0;
  
  .action-btn {
    flex: 1;
    padding: 10px;
    background: #003300;
    color: #00ff00;
    border: 1px solid #006600;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.2s;
    
    &:hover:not(:disabled) { background: #005500; }
    &:disabled { background: #222; color: #555; border-color: #333; cursor: not-allowed; }
    
    &.stealth {
      border-color: #aa00aa;
      color: #ffccff;
      background: #330033;
      &:hover:not(:disabled) { background: #550055; }
    }
  }
}

@keyframes blink { 50% { opacity: 0; } }
@keyframes scan { 0% { top: 0; } 100% { top: 100%; } }
